import React, { useState } from "react";
import { Button } from "semantic-ui-react";
import UserForm from "./UserForm";
import Question1 from "./Question1";
import Question2 from "./Question2";
import Question3 from "./Question3";
import Question4 from "./Question4";
import Thankyou from "./Thankyou";

import { BeatLoader } from "react-spinners";
import "../css/UserQuestions.css";

function UserQuestion() {
  const [postData, setPostData] = useState({
    UserName: "",
    Email: "",
    Qes1: "Assignment Medium?",
    Opt1_1: "",
    Opt2_1: "",
    Opt3_1: "",
    Opt4_1: "",
    Qes2: "Common Challenges?",
    Opt1_2: "",
    Opt2_2: "",
    Opt3_2: "",
    Opt4_2: "",
    Opt5_2: "",
    Opt6_2: "",
    Opt7_2: "",
    Qes3: "School Administration Challenges?",
    Opt1_3: "",
    Opt2_3: "",
    Opt3_3: "",
    Opt4_3: "",
    Opt5_3: "",
    Qes4: "Any Other Challenges?",
    Des_1: "",
    Des_2: "",
    Des_3: "",
    Des_4: "",
  });
  const [pageTrack, setPageTrack] = useState([
    { pageno: 0, isactive: true, compeleted: false },
    { pageno: 1, isactive: false, compeleted: false },
    { pageno: 2, isactive: false, compeleted: false },
    { pageno: 3, isactive: false, compeleted: false },
    { pageno: 4, isactive: false, compeleted: false },
  ]);
  const [isFinish, setFinish] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [pageValid, setPageValid] = useState({ isValid: false, message: "" });
  const getActivePage = () => {
    let pageNo;
    for (let i = 0; i < pageTrack.length; i++) {
      if (pageTrack[i].isactive) {
        pageNo = i;
      }
    }
    return pageNo;
  };

  const prevPageHandler = (e) => {
    e.preventDefault();
    setPageValid({
      ...pageValid,
      isValid: true,
      message: "",
    });
    const activePage = getActivePage();
    let newArray = [...pageTrack];
    newArray[activePage].isactive = false;
    newArray[activePage - 1].isactive = true;
    setPageTrack(newArray);
  };

  const nextPageHandler = (e) => {
    e.preventDefault();
    let newArray = [...pageTrack];
    const activePage = getActivePage();
    if (activePage === 0) {
      if (
        postData.Opt1_1 === "" &&
        postData.Opt2_1 === "" &&
        postData.Opt3_1 === "" &&
        postData.Opt4_1 === ""
      ) {
        setPageValid({
          ...pageValid,
          isValid: false,
          message: "Please select atleast one option from above",
        });
        return;
      }
    }
    if (activePage === 1) {
      if (
        postData.Opt1_2 === "" &&
        postData.Opt2_2 === "" &&
        postData.Opt3_2 === "" &&
        postData.Opt4_2 === "" &&
        postData.Opt5_2 === "" &&
        postData.Opt6_2 === "" &&
        postData.Opt7_2 === ""
      ) {
        setPageValid({
          ...pageValid,
          isValid: false,
          message: "Please select atleast one option from above",
        });
        return;
      }
    }
    if (activePage === 2) {
      if (
        postData.Opt1_3 === "" &&
        postData.Opt2_3 === "" &&
        postData.Opt3_3 === "" &&
        postData.Opt4_3 === "" &&
        postData.Opt5_3 === ""
      ) {
        setPageValid({
          ...pageValid,
          isValid: false,
          message: "Please select atleast one option from above",
        });
        return;
      }
    }
    if (activePage < pageTrack.length - 1) {
      newArray[activePage].isactive = false;
      newArray[activePage + 1].isactive = true;
      setPageTrack(newArray);
    }
  };
  return (
    <form>
      <div className="questions-container">
        {isFinish ? (
          <Thankyou />
        ) : isLoading ? (
          <div className="loading-div">
            <BeatLoader />
            <p>Please wait</p>
          </div>
        ) : pageTrack[4].isactive ? (
          <UserForm
            pageTrack={pageTrack}
            setPageTrack={setPageTrack}
            isFinish={isFinish}
            setFinish={setFinish}
            postData={postData}
            setPostData={setPostData}
            setLoading={setLoading}
          />
        ) : (
          <div className="qusetions">
            <div className="thumb-container">
              <div
                className={`thumbnail ${
                  pageTrack[0].isactive ? "active-compeleted" : ""
                }`}
              ></div>
              <div
                className={`thumbnail ${
                  pageTrack[1].isactive ? "active-compeleted" : ""
                }`}
              ></div>
              <div
                className={`thumbnail ${
                  pageTrack[2].isactive ? "active-compeleted" : ""
                }`}
              ></div>
              <div
                className={`thumbnail ${
                  pageTrack[3].isactive ? "active-compeleted" : ""
                }`}
              ></div>
            </div>
            {pageTrack[0].isactive ? (
              <Question1
                postData={postData}
                setPostData={setPostData}
                setPageValid={setPageValid}
                pageValid={pageValid}
              />
            ) : (
              ""
            )}
            {pageTrack[1].isactive ? (
              <Question2
                postData={postData}
                setPostData={setPostData}
                setPageValid={setPageValid}
                pageValid={pageValid}
              />
            ) : (
              ""
            )}
            {pageTrack[2].isactive ? (
              <Question3
                postData={postData}
                setPostData={setPostData}
                setPageValid={setPageValid}
                pageValid={pageValid}
              />
            ) : (
              ""
            )}
            {pageTrack[3].isactive ? (
              <Question4
                postData={postData}
                setPostData={setPostData}
                setPageValid={setPageValid}
                pageValid={pageValid}
              />
            ) : (
              ""
            )}

            {pageValid.message !== "" ? (
              <span className="validation-text">
                <i class="fa fa-exclamation-circle" aria-hidden="true"></i>
                &nbsp;
                {pageValid.message}
              </span>
            ) : (
              ""
            )}
            <br />
            <br />
            <Button.Group className="button-group">
              <Button
                onClick={prevPageHandler}
                className={`${pageTrack[0].isactive ? "disabled" : ""}`}
              >
                Previous
              </Button>
              <Button.Or />
              <Button positive onClick={nextPageHandler}>
                Next
              </Button>
            </Button.Group>
          </div>
        )}
      </div>
    </form>
  );
}

export default UserQuestion;
