import React from "react";
import { useSpring, animated } from "react-spring";

function Question2({ postData, setPostData, setPageValid, pageValid }) {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { delay: 1000, duration: 500 },
  });
  const onCheckHandler = (e) => {
    var checked = e.target.checked;
    if (checked) {
      setPageValid({
        ...pageValid,
        isValid: true,
        message: "",
      });
      if (e.target.value === "trackProblem") {
        postData.Opt1_2 = e.target.value;
      } else if (e.target.value === "manageProblem") {
        postData.Opt2_2 = e.target.value;
      } else if (e.target.value === "homeworkDelays") {
        postData.Opt3_2 = e.target.value;
      } else if (e.target.value === "keepRecord") {
        postData.Opt4_2 = e.target.value;
      } else if (e.target.value === "feedback") {
        postData.Opt5_2 = e.target.value;
      } else if (e.target.value === "allAbove") {
        postData.Opt6_2 = e.target.value;
        postData.Opt1_2 = "trackProblem";
        postData.Opt2_2 = "manageProblem";
        postData.Opt3_2 = "homeworkDelays";
        postData.Opt4_2 = "keepRecord";
        postData.Opt5_2 = "feedback";
        document.getElementById("trackProblem").checked = true;
        document.getElementById("manageProblem").checked = true;
        document.getElementById("homeworkDelays").checked = true;
        document.getElementById("keepRecord").checked = true;
        document.getElementById("feedback").checked = true;
      } else if (e.target.value === "noIssue") {
        postData.Opt7_2 = e.target.value;
      }
      setPostData(postData);
      console.log(postData);
    } else {
      if (e.target.value === "trackProblem") {
        postData.Opt1_2 = "";
        postData.Opt6_2 = "";
        document.getElementById("allAbove").checked = false;
      } else if (e.target.value === "manageProblem") {
        postData.Opt2_2 = "";
        postData.Opt6_2 = "";
        document.getElementById("allAbove").checked = false;
      } else if (e.target.value === "homeworkDelays") {
        postData.Opt3_2 = "";
        postData.Opt6_2 = "";
        document.getElementById("allAbove").checked = false;
      } else if (e.target.value === "keepRecord") {
        postData.Opt4_2 = "";
        postData.Opt6_2 = "";
        document.getElementById("allAbove").checked = false;
      } else if (e.target.value === "feedback") {
        postData.Opt5_2 = "";
        postData.Opt6_2 = "";
        document.getElementById("allAbove").checked = false;
      } else if (e.target.value === "allAbove") {
        postData.Opt6_2 = "";
        postData.Opt1_2 = "";
        postData.Opt2_2 = "";
        postData.Opt3_2 = "";
        postData.Opt4_2 = "";
        postData.Opt5_2 = "";
        document.getElementById("trackProblem").checked = false;
        document.getElementById("manageProblem").checked = false;
        document.getElementById("homeworkDelays").checked = false;
        document.getElementById("keepRecord").checked = false;
        document.getElementById("feedback").checked = false;
      } else if (e.target.value === "noIssue") {
        postData.Opt7_2 = "";
      }
      setPostData(postData);
      console.log(postData);
    }
  };

  const onChangeHandler = (e) => {
    setPostData({ ...postData, Des_2: e.target.value });
  };
  return (
    <animated.div style={props}>
      <div className="question-2">
        <p className="question-text">
          As teachers, what are the most common challenges that you face when
          using the above mentioned tool(s)?
        </p>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="trackProblem"
            value="trackProblem"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt1_2 !== "" ? true : false}
            hidden
          />
          <label for="trackProblem" className="checkmark"></label>
          <span className="option-text">
            Keeping track of homework/classwork sent to students.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="manageProblem"
            value="manageProblem"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt2_2 !== "" ? true : false}
            hidden
          />
          <label for="manageProblem" className="checkmark"></label>
          <span className="option-text">
            Manage homework sent by students and arrange them
            class/subject/student and date wise.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="homeworkDelays"
            value="homeworkDelays"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt3_2 !== "" ? true : false}
            hidden
          />
          <label for="homeworkDelays" className="checkmark"></label>
          <span className="option-text">
            Manage delays in homework sent by students.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="keepRecord"
            value="keepRecord"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt4_2 !== "" ? true : false}
            hidden
          />
          <label for="keepRecord" className="checkmark"></label>
          <span className="option-text">
            Keep a record of all assignment received from students.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="feedback"
            value="feedback"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt5_2 !== "" ? true : false}
            hidden
          />
          <label for="feedback" className="checkmark"></label>
          <span className="option-text">
            Keep a record of evaluation/feedback provided by teachers for
            individual assignments sent by the students.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="allAbove"
            value="allAbove"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt6_2 !== "" ? true : false}
            hidden
          />
          <label for="allAbove" className="checkmark"></label>
          <span className="option-text">All of the above.</span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="noIssue"
            value="noIssue"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt7_2 !== "" ? true : false}
            hidden
          />
          <label for="noIssue" className="checkmark"></label>
          <span className="option-text">
            No issues while using the current medium.
          </span>
        </div>
        <div>
          <p>Description</p>
          <textarea
            className="custom-textarea"
            id="question-2-des"
            onChange={onChangeHandler}
            value={postData.Des_2}
          />
        </div>
      </div>
    </animated.div>
  );
}

export default Question2;
