import React from "react";
import { useSpring, animated } from "react-spring";
function Question1({ postData, setPostData, setPageValid, pageValid }) {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { delay: 1000, duration: 500 },
  });
  const onCheckHandler = (e) => {
    var checked = e.target.checked;
    if (checked) {
      setPageValid({
        ...pageValid,
        isValid: true,
        message: "",
      });
      if (e.target.value === "whatsapp") {
        postData.Opt1_1 = e.target.value;
      } else if (e.target.value === "email") {
        postData.Opt2_1 = e.target.value;
      } else if (e.target.value === "googleClassroom") {
        postData.Opt3_1 = e.target.value;
      } else if (e.target.value === "anyother") {
        postData.Opt4_1 = e.target.value;
      }
      setPostData(postData);
    } else {
      if (e.target.value === "whatsapp") {
        postData.Opt1_1 = "";
      } else if (e.target.value === "email") {
        postData.Opt2_1 = "";
      } else if (e.target.value === "googleClassroom") {
        postData.Opt3_1 = "";
      } else if (e.target.value === "anyother") {
        postData.Opt4_1 = "";
      }
      setPostData(postData);
    }
  };

  const onChangeHandler = (e) => {
    setPostData({ ...postData, Des_1: e.target.value });
  };
  return (
    <animated.div style={props}>
      <div className="question-1">
        <p className="question-text">
          As teachers, what medium do you use for Studentâ€™s assignment
          (classwork/homework) Management?
        </p>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="whatsapp"
            value="whatsapp"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt1_1 !== "" ? true : false}
            hidden
          />
          <label for="whatsapp" className="checkmark"></label>
          <span className="option-text">Whatsapp.</span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="email"
            value="email"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt2_1 !== "" ? true : false}
            hidden
          />
          <label for="email" className="checkmark"></label>
          <span className="option-text">Email.</span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="googleClassroom"
            value="googleClassroom"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt3_1 !== "" ? true : false}
            hidden
          />
          <label for="googleClassroom" className="checkmark"></label>
          <span className="option-text">Google Classroom.</span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="anyother"
            value="anyother"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt4_1 !== "" ? true : false}
            hidden
          />
          <label for="anyother" className="checkmark"></label>
          <span className="option-text">
            Any other Student Assignment Management Tool.
          </span>
        </div>
        <div>
          <p>Description</p>
          <textarea
            className="custom-textarea"
            id="question-1-des"
            onChange={onChangeHandler}
            value={postData.Des_1}
          />
        </div>
      </div>
    </animated.div>
  );
}

export default Question1;
