import React from "react";
import { useSpring, animated } from "react-spring";
function Question3({ postData, setPostData, setPageValid, pageValid }) {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { delay: 1000, duration: 500 },
  });
  const onCheckHandler = (e) => {
    var checked = e.target.checked;
    if (checked) {
      setPageValid({
        ...pageValid,
        isValid: true,
        message: "",
      });
      if (e.target.value === "costly") {
        postData.Opt1_3 = e.target.value;
      } else if (e.target.value === "learningCurve") {
        postData.Opt2_3 = e.target.value;
      } else if (e.target.value === "specificReq") {
        postData.Opt3_3 = e.target.value;
      } else if (e.target.value === "notAware") {
        postData.Opt4_3 = e.target.value;
      } else if (e.target.value === "issueUsingTool") {
        postData.Opt5_3 = e.target.value;
      }
      setPostData(postData);
    } else {
      if (e.target.value === "costly") {
        postData.Opt1_3 = "";
      } else if (e.target.value === "learningCurve") {
        postData.Opt2_3 = "";
      } else if (e.target.value === "specificReq") {
        postData.Opt3_3 = "";
      } else if (e.target.value === "notAware") {
        postData.Opt4_3 = "";
      } else if (e.target.value === "issueUsingTool") {
        postData.Opt5_3 = "";
      }
      setPostData(postData);
    }
  };

  const onChangeHandler = (e) => {
    setPostData({ ...postData, Des_3: e.target.value });
  };
  return (
    <animated.div style={props}>
      <div className="question-3">
        <p className="question-text">
          As school administration, if you have used existing student assignment
          management tools like Google classroom, what do you think are the
          challenges faced?
        </p>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="costly"
            value="costly"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt1_3 !== "" ? true : false}
            hidden
          />
          <label for="costly" className="checkmark"></label>
          <span className="option-text">It is costly.</span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="learningCurve"
            value="learningCurve"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt2_3 !== "" ? true : false}
            hidden
          />
          <label for="learningCurve" className="checkmark"></label>
          <span className="option-text">
            Difficult to learn/huge learning curve for both parents and
            teachers.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="specificReq"
            value="specificReq"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt3_3 !== "" ? true : false}
            hidden
          />
          <label for="specificReq" className="checkmark"></label>
          <span className="option-text">
            Does not meet our school specific requirements.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="notAware"
            value="notAware"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt4_3 !== "" ? true : false}
            hidden
          />
          <label for="notAware" className="checkmark"></label>
          <span className="option-text">
            We are generally not aware that such online tools exists.
          </span>
        </div>
        <div className="checkbox-wrapper">
          <input
            type="checkbox"
            id="issueUsingTool"
            value="issueUsingTool"
            onClick={onCheckHandler}
            defaultChecked={postData.Opt5_3 !== "" ? true : false}
            hidden
          />
          <label for="issueUsingTool" className="checkmark"></label>
          <span className="option-text">
            Training and handholding issues when faced with queries related to
            using the tool.
          </span>
        </div>
        <div>
          <p>Description</p>
          <textarea
            className="custom-textarea"
            id="question-3-des"
            onChange={onChangeHandler}
            value={postData.Des_3}
          />
        </div>
      </div>
    </animated.div>
  );
}

export default Question3;
