import React from "react";
import { useSpring, animated } from "react-spring";
function Question4({ postData, setPostData }) {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { delay: 1000, duration: 500 },
  });
  const onChangeHandler = (e) => {
    setPostData({ ...postData, Des_4: e.target.value });
  };
  return (
    <animated.div style={props}>
      <div className="question-4">
        <p className="question-text">
          Any other challenges faced when operating online or using online tools
          for daily classroom operations?
        </p>
        <div>
          <p>Description</p>
          <textarea
            className="custom-textarea"
            id="question-4-des"
            onChange={onChangeHandler}
            value={postData.Des_4}
          />
        </div>
      </div>
    </animated.div>
  );
}

export default Question4;
